.dashboard{
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.menu{
    width: 200px;
    height: 100%;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    position: relative;
    background-color: var(--background);
    color: var(--black);
}

.menu-item{
    height: 30px;
    text-align: center;
    cursor: pointer;
    color: var(--text-light);
    -webkit-transition-property: color;
    -o-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.menu-item:hover{
    color:var(--black);
}

.menu-item.active{
    color: var(--black);
    font-weight: 500;
}

.menu-item.bottom{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;

}


